@import url(https://fonts.googleapis.com/css2?family=Glory:ital,wght@0,500;1,400&display=swap);

body {
    font-family: "Glory", sans-serif;
}

nav {
    z-index: 2;
}

.logo {
    width: 15rem;
    height: 6rem;
}

nav a.nav-link {
    font-weight: 500;
    letter-spacing: 1px;
    cursor: pointer;
}

nav ul li {
    text-transform: uppercase;
}

.navbar-nav>li>a {
    text-align: center;
    color: aliceblue !important;
    border-radius: 15px;
}

.navbar-nav>li>a.active,
.navbar-nav>li>a:hover {
    background-image: linear-gradient(to right, Crimson, orangered);
    border-radius: 15px;
}

.dropdown-menu>li>a.active,
.dropdown-menu>li>a:hover {
    color: Crimson !important;
    text-align: center !important;
    border-left: 5px solid orangered !important;
}

.dropdown-menu>li>a:focus {
    background-color: darkslategrey !important;
    color: aliceblue !important;
    text-align: center !important;
    border-left: 5px solid orangered !important;
}
.header-wrapper {
    position: relative;
    background: url(/project-js/static/media/background.fa5d0a4a.jpeg);
    background-size: cover;
    background-position: center;
    height: 90vh;
}

.main-info {
    z-index: 1;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.main-info>h1 {
    text-transform: uppercase;
    color: orangered;
}

.typed-text {
    color: aliceblue;
    font-size: 2rem;
}

.btn-main-offer {
    background-color: transparent;
    border: 2px solid Crimson;
    text-transform: uppercase;
    border-radius: 0;
    padding: 12px;
    margin: 2rem;
    color: aliceblue;
}

.btn-main-offer:hover {
    border-radius: 12px 12px;
    color: Crimson;
}

.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vh;
    z-index: 1;
}
